import {addCarCard, getYanZhengCode} from 'common/js/requestUrl'
import {request,showToast} from 'common/js/common'
import avatar from 'static/img/icon/qrcode.png'
export default {
	data() {
		return {
			showBack: false,
			placeChoosed: '浙',
			card: '',
			phone: '',
			avatar,
			yanzhengStart: false,
			yanzhengTime: 0,
			yanzhengCode: null,
			code: 0,
			cardFocus: false,
			Timer : null,
			placeList:[
				"京", 
				"津", 
				"沪",
				"渝",
				"冀",
				"吉",
				"辽", 
				"黑",
				"湘",
				"鄂",
				"甘",
				"晋",
				"陕",
				"豫",
				"川", 
				"云", 
				"桂",
				"蒙",
				"贵", 
				"青",
				"藏",
				"新", 
				"宁", 
				"粤",
				"琼",
				"闽", 
				"苏",
				"浙", 
				"赣",  
				"鲁",  
				"皖"]
		};
	},
	onLoad:function(){
		
	},
	methods:{
		cancleBack(){
			this.showBack = false
		},
		showsBack(){
			this.showBack = true
		},
		chooseItem(item){
			this.placeChoosed = item
			this.showBack = false
		},
		inputCard(e){
			this.card = e.detail.value.toUpperCase()
		},
		inputPhone(e){
			this.phone = e.detail.value
		},
		inputYanzhengCode(e){
			this.yanzhengCode = e.detail.value
		},
		changCardToUpper(){
			this.card = this.card.toUpperCase()
		},
		changForce(){
			this.cardFocus = true
		},
		cardUnFocus(){
			this.cardFocus = false
		},
		getCode(){
			if(!this.yanzhengStart){
				if(!this.phone){
					showToast('您还未输入手机号')
					return
				}
				request(getYanZhengCode,{
					phone: this.phone
				},(res) => {
					if(res.code == 200){
						this.yanzhengStart = true
						this.yanzhengTime = 30
						this.Timer = setInterval(this.timeGone , 1000)
						this.code = res.result
					}
				})
			}
		},
		showModel(){
			this.showBack = true
		},
		timeGone(){
			this.yanzhengTime -= 1
			if(this.yanzhengTime == 0){
				this.yanzhengStart = false
				clearInterval(this.Timer)
			}
		},
		confirm(){
			if(!this.placeChoosed){
				showToast('您还未选择车牌前缀')
				return
			}
			if(!this.phone){
				showToast('您还未输入手机号')
				return
			}
			if(!this.card){
				showToast('您还未输入车牌号')
				return
			}
			if(!this.yanzhengCode){
				showToast('您还未输入验证码')
				return
			}
			if(this.code != this.yanzhengCode){
				showToast('验证码有误，您可以稍后重新获取验证码')
				return
			}
			request(addCarCard,{
				card: this.card,
				phone: this.phone,
				prefix: this.placeChoosed
			},function(res){
				if(res.code == 200){
					showToast('添加成功','success')
					uni.navigateBack({
						delta: 1
					});
				}
			})
		}
	}
}
